import {FactoryProvider, InjectionToken} from "@angular/core";
import {IRepository} from "../../../../domain/interfaces/irepository.interface";
import {ProfileBasicService} from "./profilebasic.service";
import {ProfileRequest} from '../../../../domain/models/profile/profilerequest.model';

export const ProfileSpesificToken = new InjectionToken<IRepository<ProfileRequest>>('profilespesific.service');

export class ProfileSpesificProvider implements FactoryProvider {
  public provide: InjectionToken<IRepository<ProfileRequest>> = ProfileSpesificToken;
  public deps: any[] = [ProfileBasicService];
  public multi: boolean = false;
  public useFactory: Function = (...deps: IRepository<ProfileRequest>[]) => {
    return deps[0]
  };
}